.elements {
  max-width: 880px;
  margin: 0 auto;
  padding: 25px 0 30px 0;
  background-color: #000000;
}

@media screen and (max-width: 1023px) {
  .elements {
    padding: 18px 19px 24px 19px;
  }
}

@media screen and (max-width: 319px) {
  .elements {
    padding: 18px 0px 24px 0px;
  }
}