.form__input {
  margin: 17px 0 0 0;
  padding: 13px 0 13px 0;
  width: 358px;
  height: 14px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
}



@media screen and (max-width: 767px) {
  .form__input {
    width: 238px;
  }
}